import * as React from 'react';
import { useDispatch } from "react-redux";
import { useNavigate } from 'react-router-dom'

import closeSvg from './../assets/images/close-white.svg'
import { LOG_OUT } from '../redux/constants/actionTypes';
import { useLogoutMutation } from '../features/auth/slices/authApiSlice';

export default function MobileExpandedMenu(props) {
    const dispatch = useDispatch();
    const navigate = useNavigate()
    const [logout] = useLogoutMutation()

    const handleLogOut = async (e) => {
        try {
          const responseData = await logout().unwrap()
        } catch (err) {
          console.log("err", err)
        }
        dispatch({ type: LOG_OUT })
        navigate('/', { replace: true });
      }
          
    return (
        <div className={props.show ? 'expanded__menu shown' : 'expanded__menu'}>
            <div className="menu-wrapper">
                <button className="close-menu sidebarToggler" onClick={props.onClose}>
                    <img src={closeSvg} alt="Close Sidebar" />
                </button>

                <div className="menu-navigation">
                    <ul>
                        <li>
                            <a href="/m/membre">Products</a>
                        </li>
                        <li>
                            <a href="/m/my-shopping-list">My shopping list</a>
                        </li>
                        <li>
                            <a href="javascript:void(0)" disabled>
                                My Account
                                <p>
                                    Access your account settings and payment history on the desktop version of 100grammes
                                </p>
                            </a>
                        </li>
                        <li>
                            <a href="https://www.100grammes.ca/faq#liste" target="_blank" rel="noopener noreferrer">FAQ</a>
                        </li>
                        <li>
                            <a href="https://www.100grammes.ca/termes-et-conditions" target="_blank" rel="noopener noreferrer">Terms and Conditions</a>
                        </li>
                        <li>
                            <a onClick={handleLogOut}>Sign off</a>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    );
}