import React, { useState } from 'react';
import { useDispatch, connect } from "react-redux";
import { useNavigate } from 'react-router-dom'
import vertPng from './../../../assets/images/100g_vert.png'
import menuSvg from './../../../assets/images/menu.svg'
import closeSvg from './../../../assets/images/close.svg'
import { getFixedT } from 'i18next';

import MobileExpandedMenu from '../../../components/MobileExpandedMenu';
import { ADD_REMOVE_CART } from '../../../redux/constants/actionTypes';
import { useRemoveCartMutation } from '../slices/membreApiSlice';
import SadComponent from '../../../components/SadComponent';

const mapStateToProps = (state) => ({
    carts: state.product.carts
});

const MobileMyShoppingList = ({ carts }) => {
    const t = getFixedT(null, null, 'member')
    const dispatch = useDispatch();
    const navigate = useNavigate()
    const [menuShow, setMenuShow] = useState(false)
    const [removeCart] = useRemoveCartMutation()

    const calculateTotalPerSource = (cart) => {
        var total = 0;
        for (let i = 0; i < cart.length; i++) {
            if (cart[i].final_price_for_item) {
                total += Number(cart[i].final_price_for_item)
            }
        }
        const formattedNumber = total.toLocaleString('en-US', { style: 'currency', currency: 'USD' });
        return formattedNumber;
    }
    
    let totalCartNum = 0;
    for (let key in carts) {
        totalCartNum += carts[key].length
    }

    const handleRemoveCart = (product) => {
        removeCart({ url: product.url }).unwrap().then(function (res) {
            console.log(res)
        })
        dispatch({ type: ADD_REMOVE_CART, payload: { product: product } })
    }

    return (
        <>
            <header className='shopping-header'>
                <div class="top_nav">
                    <div></div>
                    <div class="logo">
                        <a href="https://www.100grammes.ca">
                            <img src={vertPng} alt="100grammes" />
                        </a>
                    </div>
                    <div class="hamburger">
                        <div class="cart-count">
                            {totalCartNum}
                        </div>
                        <button class="sidebarToggler" onClick={() => setMenuShow(true)}>
                            <img src={menuSvg} alt="Menu" />
                        </button>
                    </div>
                </div>
                <h5 class="header-title font-pacifico text-white text-center ">
                    My shopping list
                </h5>
            </header>
            <MobileExpandedMenu show={menuShow} onClose={() => setMenuShow(false)} />
            <div class="apps-body">
                <div class="grocery-cart-list">
                    <div class="widget">
                        {Object.keys(carts).map((key, index) => (
                            <>
                                <h3 class="widget-title text-center">{key} {calculateTotalPerSource(carts[key])}</h3>
                                <ul>
                                    {carts[key].map((cart) => (

                                        <li>
                                            <div className="img">
                                                <a href={cart.url} target="_blank" rel="noopener noreferrer">
                                                    <img className='cart-img' src={cart.image} width={40} alt={cart.final_name} />
                                                </a>
                                            </div>
                                            <div className="text cart-product-name">
                                                <a href={cart.url} target="_blank" rel="noopener noreferrer">
                                                    {cart.final_name} {cart.brand}
                                                </a>
                                                <p className='cart-unit-price'>
                                                    ${cart.final_price_per_unit_of_measure} {cart.final_unit_of_measure} | ${cart.final_price_for_item}
                                                </p>
                                            </div>
                                            <button className="action" title="remove" onClick={() => handleRemoveCart(cart)}>
                                                <img src={closeSvg} alt="Remove" />
                                            </button>
                                        </li>

                                    ))}
                                </ul>
                                <hr></hr>
                            </>
                        ))}
                        {totalCartNum == 0 &&
                            <SadComponent text={t('Your cart feel sad and lonely. Add stuff to it')} />
                        }

                    </div>
                </div>
            </div>

        </>

    );
}

export default connect(mapStateToProps)(MobileMyShoppingList);