import React from 'react';
import {ImSad2} from "react-icons/im"
import './../assets/css/sad-component.css'; // Replace with the path to your CSS file
import attentePng from './../assets/images/attente.png' 

const SadComponent = (props) => {
  return (
    <div className="sad-component">
        <div>
          <img src={attentePng} />
        </div>
        <div>
            <p className='sad-text'>
              {props.text}
            </p>
        </div>
    </div>
  );
};

export default SadComponent;